import React, { Component } from "react";
import wrapper_clickFun from "../../components/wrapperClickFun";
import ListPageTable from "../../pages/listPageTable";

const API_form_name = "sampleTestResult";
const form_name = "Sample Test Result List";
const Create_Update_Path = `/${process.env.REACT_APP_NAME}/${API_form_name}/CreateUpdate`;
const listAPI = `/${process.env.REACT_APP_NAME}/${API_form_name}/ListAPI/`;
const form_type = "Master";

const tableHeaderList = [
    {cn: "Report No", fv: "code", fn: "report_no", st: "search_box", sv: "report_no"},
    {cn: "Sample Register No", fv: "sample_registration", fn: "sample_registration", st: "search_box", sv: "sample_registration"},
    {cn: "Attention", fv: "attention", fn: "attention", st: "search_box", sv: "attention"},
    {cn: "Organisation", fv: "organisation", fn: "organisation", st: "search_box", sv: "organisation__org_desc"},
    {cn: "Status", fv: "status", fn: "status", st: "search_box", sv: "status"},
    {cn: "Last Updated By",fv: "User Name",fn: "updated_by",st: "search_box",sv: "updated_by__full_name"},
    {cn: "Last Updated Date",fv: "Date",fn: "updated_date",st: "datepicker",sv: "updated_date",},
];

class listPage extends Component {
    render() {
        return (
            <div className="wrapper">
                <div className="content-wrapper" onClick={wrapper_clickFun.bind(this)}>
                    <ListPageTable tableHeaderList={tableHeaderList} listAPI={listAPI} to_page={Create_Update_Path} generatePDF={true} form_name={form_name} apiFormName={API_form_name} form_type={form_type}></ListPageTable>
                </div>
            </div>
        );
    }
}

export default listPage;
